import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./registerServiceWorker";
import { apolloProvider } from "../graphql/apollo";
import { MdTable, MdCard, MdContent, MdToolbar, MdButton, MdIcon, MdDrawer, MdList } from "vue-material/dist/components";
import "vue-material/dist/vue-material.min.css";
import "vue-material/dist/theme/default.css";

Vue.use(MdTable);
Vue.use(MdCard);
Vue.use(MdContent);
Vue.use(MdToolbar);
Vue.use(MdButton);
Vue.use(MdIcon);
Vue.use(MdDrawer);
Vue.use(MdList);

Vue.config.productionTip = false;

new Vue({
    router,
    apolloProvider,
    store,
    render: h => h(App)
}).$mount("#app");
